import utils from '../tools/utils'

export const validRules = {
    username: {
        required: {
            rule: true,
            message: ''
        }
    },
    bankCard: {
        length: {
            rule: 12,
            message: ' 12 '
        }
    },
    phone: {
        length: {
            rule: 11,
            message: ' 11 '
        }
    },
    password: {
        minlength: {
            rule: 6,
            message: ' 6 - 20 '
        },
        maxlength: {
            rule: 20,
            message: ' 6 - 20 '
        }
    },
    code: {
        length: {
            rule: 6,
            message: ''
        }
    },
    idcard: {
        length: {
            rule: 18,
            message: ' 18 '
        }
    }
}

export const validators = {
    eq (val, target) {
        return parseInt(val, 10) === parseInt(target, 10)
    },
    atLeast (val, target) {
        return parseInt(val, 10) >= parseInt(target, 10)
    },
    length (val, target) {
        return val.length === parseInt(target, 10)
    }
}

export function toValidate(cb) {
    // 
    this.$validate(true)
    if (this.$validator.invalid) {
        const errorsList = this.$validator.errors.reverse()
        let errors = ''
        for (const i in errorsList) {
            if ({}.hasOwnProperty.call(errorsList, i)) {
                errors += `${errorsList[i].message}<br>`
            }
        }
        utils.alert(errors)
    } else {
        cb()
    }
}



